<template>
  <router-link :to="{ name: 'userverify' }">Home</router-link> |
  <router-link :to="{ name: 'profile' }">Profile</router-link> |
  <router-link :to="{ name: 'content' }">Content</router-link> |
  <router-link :to="{ name: 'login' }">Store</router-link> |

  {{ nation_info }} {{ store_info }} {{ category_info }} {{ product_info }}
  {{ email_info }}

<div>{{ profile.user_profile }}</div>
  <hr />

  <!-- modal -->
  <v-dialog v-model="user.user_modal">
    <v-card>
      <v-card-text>
        You are first time here. Would you like to make it?
      </v-card-text>
      <v-card-actions class="justify-end" color="orange">
        <v-btn variant="outlined" color="black" @click="user.user_modal = false"
          >No</v-btn
        >
        <v-btn variant="outlined" color="error" @click="create_account_btn(); user.user_modal = false;"
          >Yes</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
  <!-- modal -->

  <v-container class="ma-0 pa-0 store-background" fluid>
    <v-row class="justify-center align-center ma-0 pa-0" style="height: 100vh">
      <v-col class="d-flex justify-center ma-0 pa-0">
        <v-card elevation="20" rounded="0" color="black" width="400">
          <v-img
            class="justify-end align-center"
            cover
            height="100%"
            :src="store_return.file_login"
          >
            <v-card-text style="text-align: center" class="text-black">
              <div>
                <p class="text-h4">{{ store_return.store_title }}</p>
                <p>{{ store_return.store_intro }}</p>
                <p>{{ store_return.store_phone }}</p>
                <p>{{ store_return.store_address }}</p>
              </div>
            </v-card-text>
          </v-img>
        </v-card>
        <v-card
          elevation="20"
          rounded="0"
          color="white"
          width="400"
          class="pa-2 pl-4"
        >
          <v-card-item>
            <div>
              <div class="text-overline mb-1">
                Welcome to {{ store_return.store_title }}
              </div>
              <div class="text-h6 mb-1">Access File System</div>
              <div class="text-caption">
                We are contacting you based on your email and phone
              </div>
              <v-text-field
                class="mt-5"
                prepend-icon="mdi-email-open-outline"
                color="primary"
                label="Email"
                type="email"
                :rules="emailRules"
                v-model="this.user_info.user_mail"
                variant="underlined"
              ></v-text-field>

              <v-text-field
                prepend-icon="mdi-phone-outline"
                v-model="this.user_info.user_phone"
                color="primary"
                label="Phone"
                :rules="numberRule"
                variant="underlined"
              ></v-text-field>
            </div>
          </v-card-item>
          <v-card-actions class="justify-end pa-5">
            <v-btn variant="outlined" @click="login"> Login </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
  {{ user }}
  <hr />
  {{ profile }}
  <hr />
  {{ content }}
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import http from "@/api/http.js";

export default {
  name: "App",
  data() {
    return {
      newModal: false,
      // local user email, phone, id
      user_info: {
        user_mail: null,
        user_phone: null,
        store_id: null,
      },
      // /:nation/:store/:category/:product/:email
      // local nation, store, category, product, email( These will be passed to user/store)
      nation_info: this.$route.params.nation || 82,
      store_info: this.$route.params.store || 1,
      category_info: this.$route.params.category || 0,
      product_info: this.$route.params.product || 0,
      email_info: this.$route.params.email || false,

      // this is for the Front Page information
      store_return: {},
      entry_setup : {},
      //
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
      numberRule:[
        v => (!isNaN(parseFloat(v)) && v >= 0 && v <= 99999999999999) || 'Only Number Needed'
      ]
    };
  },
  created() {
    this.init();
  },
  async mounted() {
    await http.getEntrySetup( ).then((resp) => {
        this.entry_setup = resp.data;})

    if (this.email_info != false) {
      localStorage.removeItem("user_mail");
      localStorage.removeItem("user_phone");
      localStorage.removeItem("userid");
      this.user_info.user_mail = this.email_info 
      this.$store.dispatch("user/user_get", {
        user_mail: this.email_info,
        user_phone: null,
        nation : this.nation_info,
        store : this.store_info,
        product_category : this.category_info,
        product_type : this.product_info,
        // option to login
        modal : this.entry_setup.url_popup,
        no_create : this.entry_setup.url_no_create,
      });
    }

    if (this.user.user_status != true) {
      this.$store.dispatch("user/init_user");
    }
  },
  computed: {
    ...mapState(["user", "content", "profile"]),
  },
  components: {},
  methods: {
    ...mapMutations(["remove_user_status"]),
    ...mapActions(["user_get"]),
    async init() {
      await http.setNationStore(this.store_info).then((resp) => {
        this.store_return = resp.data;
        this.$store.commit( 'profile/set_store_data', resp.data)
      });
    },
    login() {
      this.$store.dispatch("user/user_get", {
        userid: this.user_info.userid,
        user_mail: this.user_info.user_mail,
        user_phone: this.user_info.user_phone,
        nation : this.nation_info,
        store : this.store_info,
        product_category : this.category_info,
        product_type : this.product_info,
        // option to login
        modal : this.entry_setup.login_popup,
        no_create : this.entry_setup.login_no_create,
      });
    },
    create_account_btn(){
      this.$store.dispatch("user/make_user", {
        user_mail: this.user_info.user_mail,
        user_phone: this.user_info.user_phone,
        nation : this.nation_info,
        store : this.store_info,
        product_category : this.category_info,
        product_type : this.product_info
      });
    }
  },
};
</script>
<style>
.store-background {
  background-image: radial-gradient(#ff7300, #e52e71);
}
</style>



